:root {
  --primary-color: #F15A24;
  --orange-faded:  #FF7F4F;
  --orange-white:  #FF9D78;
  --orang-black:  #9E2B00;
  --orange-shaded: #C73D0A;
  --negative: #C10015;
  --gray: #5f6368;
  --shaded-gray: #303134;
  --background-main-color: #282c34;
}
* {
  margin: 0;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34 !important;
  background-color: var(--background-main-color) !important;
  font-size: calc(10px + 1vmin) !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.mr-1 {
  margin-right: 5px
}

.ml-2 {
  margin-left: 10px;
}

.cursor-pointer {
  cursor: pointer;
}

.ReactModal__Overlay {
  background-color: rgba(0, 0, 0, 0.75) !important;
}

.App {
    text-align: center;
  }
  
.App-logo {
    margin-top: 1em;
    height: 40vmin;
}

.App-logo:hover {
    -webkit-animation-name: twis-dance;
            animation-name: twis-dance;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-delay: 0;
            animation-delay: 0;
    -webkit-animation-direction: alternate;
            animation-direction: alternate
}
.App-header {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: var(--orange-white);
}
.Main-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}
@-webkit-keyframes twis-dance {
    0%   {}
    12.5%  {-webkit-transform: rotate(-5deg);transform: rotate(-5deg);}
    25%  {-webkit-transform: rotate(-5deg);transform: rotate(-5deg);}
    37.5% {-webkit-transform: rotate(5deg);transform: rotate(5deg);}
    50%   {-webkit-transform: rotate(5deg);transform: rotate(5deg);}
    62.5%  {-webkit-transform: rotate(-5deg);transform: rotate(-5deg);}
    75%  {-webkit-transform: rotate(-5deg);transform: rotate(-5deg);}
    87.5% {-webkit-transform: rotate(5deg);transform: rotate(5deg);}
    100%  {-webkit-transform: rotate(-5deg);transform: rotate(-5deg);}
}
@keyframes twis-dance {
    0%   {}
    12.5%  {-webkit-transform: rotate(-5deg);transform: rotate(-5deg);}
    25%  {-webkit-transform: rotate(-5deg);transform: rotate(-5deg);}
    37.5% {-webkit-transform: rotate(5deg);transform: rotate(5deg);}
    50%   {-webkit-transform: rotate(5deg);transform: rotate(5deg);}
    62.5%  {-webkit-transform: rotate(-5deg);transform: rotate(-5deg);}
    75%  {-webkit-transform: rotate(-5deg);transform: rotate(-5deg);}
    87.5% {-webkit-transform: rotate(5deg);transform: rotate(5deg);}
    100%  {-webkit-transform: rotate(-5deg);transform: rotate(-5deg);}
}

.searchField {
    display: flex;
    justify-content: center;
}

.searchField div.input-group {
    width: 80vmin;
    border: 1px solid var(--gray);
    border-radius: 24px;
}

.searchField div.input-group:hover {
    background-color: var(--shaded-gray);
    box-shadow: 0 1px 6px 0 #171717;
    border-color: rgba(223,225,229,0);
}

.searchField input {
    flex: 1 1;
    border: none;
    box-shadow: none;
    background: none;
    color: white;
}
.searchField input:focus-visible {
    outline: none;
}

.searchField div.input-group div.input-group-prepend span {
    background: none;
    border: none;
    color: white;
}

.searchButton {
    width: 40%;
    max-width: 130px;
    border: none;
    background-color: var(--shaded-gray);
    color: var(--orange-white);
    border-radius: 5px;
}

.searchButton:hover {
    border: 1px solid var(--gray)
}

.searchButton:nth-child(1) {
    margin-right: 1rem;
}

.alert {
    padding: 1rem 1rem;
    margin-top: 1rem;
    margin-bottom: 1rem;
    border: 1px solid transparent;
    border-radius: 0.25rem;
    cursor: pointer;
  }
  .error {
    color: white !important;
    background-color: var(--negative) !important;
    border-color: var(--gray) !important;
  }
  .closebtn {
    margin-left: 15px;
    color: white;
    font-weight: bold;
    float: right;
    font-size: 22px;
    line-height: 20px;
    cursor: pointer;
    transition: 0.3s;
  }
  .alert:hover {
    background-color: var(--shaded-gray) !important;
    box-shadow: 0 1px 6px 0 #171717;
    border-color: rgba(223,225,229,0);
  }

  .hide {
    display: none;
  }
